// styles go here
html {
  font-family: Cambria, Georgia, serif;
}

.content {
  padding: 20px;
  width: 320px;
  margin: 50px;
  border: 2px solid rgb(112, 109, 109);
  border-radius: 30px;
  background-color: rgb(247, 219, 181);

  &__title {
    font-size: 40px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
  }
}

.list {
  padding: 0;

  &__item {
    display: inline;
    list-style: none;
    font-size: 24px;
    padding: 18px;
    border-radius: 20px;

    &--isSelected {
      background-color: yellowgreen;
    }
  }
}

.button {
  padding: 10px;
  width: 100px;
  color: white;
  font-weight: bold;
  border: 1px solid rgb(44, 187, 248);
  background-color: rgb(44, 187, 248);
  border-radius: 30px;

  &__remove {
    border: 1px solid rgb(255, 48, 69);
    background-color: rgb(255, 48, 69);
  }
}
